import './App.css';
import React from 'react';
import { HashRouter, Route } from "react-router-dom";
import IncorporationForm from './components/Main';

    function App() {
        return (
            <HashRouter basename="/">
                <div>
                    <Route exact path="/" render={props => <IncorporationForm {...props} />} /> 
                    <Route exact path="/insta" render={props => <IncorporationForm {...props} />} /> 
                </div>
            </HashRouter>
        );
}

export default App;